var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test('fail open', function () {
  return request.post('/')
    .send({
      "host": "1.1.1.999",
      "user": "admin",
      "pass": "admin"
    })
    .expect(400);
});

test('close not opened', function (t) {
  return request.post('/')
    .send({
      host: "197.211.1.1",
      close: 1
    })
    .expect(400);
});
